router.beforeEach((to, from, next) => {
      // console.log(to)
      const authUser = store.getters.IS_AUTHENTICATION
      if (to.meta.auth) {
        if (authUser) {
            const permission =  store.state.UserRoleId
            if(permission === to.meta.permission){
               next()
            }else{
               next({path: '/'})
            }
        } else {
          next({path: '/login'})
        }
      }else if(to.meta.PgLogin){
            if(authUser){
                next({path: '/'}) 
            }else{
                  next()  
            }
      }
      next()
})

export default router;



